import React from 'react'
import styled from 'styled-components'
import Header from "./Header"

const Nav = styled.div`
    width: 100%;
    height: 50px;
    background-color: black;
    border: 2px groovy black;
    z-index: 2;
    position: fixed;
    left: 0;
    top: 0;
`

const Navbar = () => {
    return (
        <Nav>
            <Header/>
        </Nav>
    )
}

export default Navbar
