import React from 'react'
import {useState, useEffect} from 'react'
import Item from './Item'
import styled from 'styled-components'
import Draggable from 'react-draggable';
import axios from 'axios'

const FindItemStyled = styled.div`
    min-width: 400px;
    border: 2px solid black;
    display: ${({ showFindItem }) => showFindItem ? 'none' : 'flex'};
    flex-wrap: wrap;
    bacground-color: white;
    margin: 2px;
`

const FindItem = ({items, onDelete, setShowFindItem, showFindItem, drag, setDrag}) => {
    const [findId, setFindId] = useState('')
    const [isError, setIsError] = useState(false);
    const [foundId, setFoundId] = useState('')
    const [isFound, setIsFound] = useState(false)
    const [url, setUrl] = useState(
        'http://localhost:3000/items/1'
        );
       
    useEffect(() => {
        const fetchData = async () => {
            setIsError(false);
            try {
                const result = await axios(url);
                setFoundId(result.data);
                setIsFound(true)
            } catch (error) {
                setIsError(true);
                setIsFound(false)
            }   
        };
       
        fetchData();
    }, [url]);
    

    return (
        <Draggable bounds="parent">
            <FindItemStyled>
            <div className="find-item-container">
                <div className='label-container'>
                    <div className="label">Find item</div><div className="label-close" onClick={()=>{setShowFindItem(!showFindItem)}}>X</div>
                </div>
                <form className="find-item-form" >
                    <div className="form-control">
                        <label>Type an item ID</label>
                        <input 
                            type="text"
                            value={findId}
                            onChange={(e)=>setFindId(e.target.value)}
                        />
                        <button className="btn" type="button" value="Search" onClick={()=> setUrl(`http://localhost:3000/items/${findId}`)}> SEARCH </button>
                    </div>
                </form>
            {isFound &&
            <div id="foundItem">
                <Item item={foundId} onDelete={onDelete}/>
            </div>}
            </div>   
            </FindItemStyled>
        </Draggable> 
    )
}

export default FindItem
